import React from "react"
import Header from "../components/Header"
import styled from "styled-components"
import "../components/layout.css"

const ProjectForm = styled.form`
  display: flex;
  flex-direction: column;
`

const ProjectTitle = styled.h1`
  font-size: 70px;
  margin-bottom: 10px;
  text-align: center;
  @media (min-width: 768px) and (max-width: 1024px) {
    font-size: 64px;
  }
  @media (min-width: 200px) and (max-width: 767px) {
    font-size: 46px;
  }
`

const ProjectLabel = styled.label`
  text-align: center;
  margin: 1rem;
`

const ProjectInput = styled.input`
  width: 300px;
  height: 30px;
  padding: 0.5rem;
  outline: none;
  letter-spacing: 1px;
  background-color: #dbd5d5;
  border: none;
  margin: 5px auto 15px auto;
  border-radius: 2px;

  @media (min-width: 200px) and (max-width: 767px) {
    width: 200px;
    height: 20px;
  }
`

const ProjectTextarea = styled.textarea`
  width: 300px;
  height: 120px;
  padding: 0.5rem;
  resize: none;
  margin: 0 auto;
  border: none;
  outline: none;
  letter-spacing: 1px;
  background-color: #dbd5d5;
  border-radius: 2px;
  @media (min-width: 200px) and (max-width: 767px) {
    width: 200px;
    height: 60px;
  }
`

const ProjectButton = styled.button`
  width: 175px;
  cursor: pointer;
  color: #fff;
  border: none;
  outline: none;
  font-weight: 500;
  height: 50px;
  background-color: #252d4f;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  -webkit-box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  margin: 20px auto 0 auto;
  @media (min-width: 200px) and (max-width: 767px) {
    width: 150px;
    height: 40px;
  }
`

const contact = () => {
  return (
    <>
      <Header />
      <div className="wrapper">
        <ProjectTitle>Contact</ProjectTitle>
        <ProjectForm
          action="https://getform.io/f/ecd04692-fbbe-48a0-9426-e2ca7d321c44"
          method="POST"
        >
          <ProjectLabel htmlFor="name">Your Name</ProjectLabel>
          <ProjectInput name="name" type="text" id="name" required />
          <ProjectLabel htmlFor="email">Your Email</ProjectLabel>
          <ProjectInput name="email" type="email" id="email" required />
          <ProjectLabel htmlFor="message">Your Message</ProjectLabel>
          <ProjectTextarea name="message" type="text" id="message" required />
          <ProjectButton type="submit">Send Message</ProjectButton>
        </ProjectForm>
      </div>
    </>
  )
}

export default contact
